import React, { Component } from "react";
import { TabBar, Button, NavBar, Icon } from "antd-mobile";
import {
  HashRouter as Router,
  Switch,
  Link,
  Route,
  withRouter
} from "react-router-dom";
import { routes } from "./data";
import { setStore, getStore, removeStore } from "util/store";

const sharePath = ["/share-collection", "/exam-collection"];
export default class index extends Component {
  componentDidMount() {
    let flag = sharePath.includes(this.props.location.pathname);
    log(flag, this.props.location.pathname);

    if (!flag) {
      let token = getStore("token");
      if (!token) {
        this.props.history.push("/login");
      }
    }
  }

  render() {
    const router = (
      <Switch>
        {routes.map((route, index) => (
          <Route
            key={`route-${index}`}
            exact={route.exact}
            path={route.path}
            component={route.page}
          />
        ))}
      </Switch>
    );
    return <div className="home-container">{router}</div>;
  }
}
